/*  Sidebar Odontograma*/

.sidebar-hallazgos {
    position: absolute;
    top: 400px;
    right: 20px;
    padding: 52px 0 0 0;
}
.opciones-odontologia {
    background-color: white;
    height: 237px;
    width: 240px;
    // position: fixed;
    // top: 150px;
    // position: absolute;
    // top:400px;
    // right: 20px;
    border: 1px solid gray;
    padding: 5px;
     overflow-x: auto;
    z-index: 100;
    h5 {
        margin:0 0 10px 0 ;
        font-size: 13px;
      }
    p {
        font-size: 12px;
        font-weight: bold;
        color:green;
