//Fractura


  .box-opciones {
    display: flex;
    height: 30px;
    background: rgba(25,0,0,0.1);
    position: relative;
    > div {
      position:relative;
      border: 1px solid gray;
      margin: 0 4px 0 0;
    }
  }
  .linea-horizontal-top {
    width: 25px;
    height: 22px;
    background: white;
    &:before {
      content: '';
      position: absolute;
      width: 20px;
      height: 3px;
      background: red;
      top: 4px;
      left: 2px;
    }
  }
  .linea-horizontal-sub {
    width: 25px;
    height: 22px;
    background: white;
    &:before {
      content: '';
      position: absolute;
      width: 20px;
      height: 3px;
      background: blue;
      top: 8px;
      left: 2px;
    }
  }

